@import 'components/variables';

.section-login {
  padding-top: 50px;
  background-image: url(../images/team/bg-team-mobile.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  @media (min-width: 992px) {
    background: #efeff7;
    background-image: url(../images/team/bg-team.png);
    background-position: center top;
    background-repeat: no-repeat;
    background-size: 100% auto;
    padding-top: 150px;
    padding-bottom: 3rem;
  }
  .content-container {
    background-color: transparent;
    padding: 0;
    @media (max-width: 991.98px) {
      max-width: 100%;
    }
    @media (min-width: 576px) {
      border-radius: 25px;
    }
    @media (min-width: 992px) {
      background: $white;
    }
  }
  .container1 {
    background: transparent;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    @media (min-width: 992px) {
      background: $white;
    }
    @media (max-width: 991.98px) {
      p {
        color: $white;
      }
    }
  }
  .container2 {
    background: $white;
    @media (max-width: 991.98px) {
      max-width: 100%;
    }
    @media (min-width: 992px) {
      border-bottom-left-radius: 25px;
      border-bottom-right-radius: 25px;
      padding-bottom: 1.5rem;
    }
    .form-login {
      margin: auto;
      margin-bottom: 2rem;
      margin-top: 2rem;
      text-align: center;
      @media (min-width: 992px) {
        margin-top: 0rem;
      }
      .form-control {
        padding: .8rem .75rem;
      }
      .username {
        background-image: url(../images/login/user.png);
        background-repeat: no-repeat;
        background-position: 9px;
        background-size: auto 70%;
        text-align: center;
        border-radius: 10px;
        &:focus {
          border: 1px solid $gray-light;
          box-shadow: 0 0 0 2px rgba(54, 69, 64, .3);
        }
      }
      .input-pass {
        position: relative;
        .password {
          display: inline;
          background-image: url(../images/login/pass.png);
          background-repeat: no-repeat;
          background-position: 10px;
          background-size: auto 70%;
          border-radius: 10px;
          text-align: center;
          &:focus {
            border: 1px solid $gray-light;
            box-shadow: 0 0 0 2px rgba(54, 69, 64, .3);
          }
        }
        .eyes-hide,
        .eyes-show {
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          transition: all .3s;
          background: transparent;
          border: none;
          img {
            height: 70%;
          }
        }
        .eyes-hide {
          display: none;
          transition: all .3s;
        }
        .eyes-show {
          display: block;
          transition: all .3s;
        }
      }
      .btn-redgr-white,
      .btn-white-redgr {
        width: 100%;
        text-align: center;
        border-radius: 10px;
        padding: .8rem .75rem;
      }
      .enlace {
        color: $orange;
        text-decoration: none;
        transition: all .3s;
        &:hover {
          color: $red;
        }
      }
      .btn-white-redgr {
        color: $text-gray;
        border: 1px solid #ced4da;
        font-size: 1rem;
        &:hover {
          background-color: $gray-light;
        }
      }
      .gmail {
        background-image: url(../images/login/gmail.png);
        background-repeat: no-repeat;
        background-position: 10px;
        background-size: auto 80%;
      }
      .facebook {
        background-image: url(../images/login/facebook.png);
        background-repeat: no-repeat;
        background-position: 10px;
        background-size: auto 80%;
        margin-top: 1rem;
      }
      .enlace-redes {
        color: $text-gray;
        text-decoration: none;
        transition: all .3s;
        font-size: .8rem;
        @media (min-width: 768px) {
          font-size: 1rem;
        }
        &:hover {
          color: $orange;
        }
      }
    }
  }
}

